.blog {
  .ms-item {
    padding: 0;
    border-radius: 5px;
    overflow: hidden;
    .news-gallery-post .left-news-slider-arrow, .news-gallery-post .right-news-slider-arrow {
      top: 45%;
    }
    video {
      width: 100% !important;
    }
    .ms-item-text {
      padding: 1rem;
    }
  }
  .post-title {
    font-weight: bold;
  }
  .blogPostMeta {
    text-transform: uppercase;
    font-size: 0.8rem;
    margin-bottom: 0.5rem;
  }
  .read-more-button {
    margin-bottom: 1rem;
    font-family: 'Open Sans';
  }
}